<template>
    <div>
        <Row class="plr10">
            <Col :xs="24" :sm="12" :md="6">
                <Card :dis-hover="true" class="mt20 mlr10">
                    <div>实时交易</div>
                    <div style="height:60px;"></div>
                    <div>
                        <div>日均销售额<span class="value">￥12,423</span></div>
                    </div>
                </Card>
            </Col>
            <Col :xs="24" :sm="12" :md="6">
                <Card :dis-hover="true" class="mt20 mlr10">
                    <div>实时交易</div>
                    <div style="height:60px;"></div>
                    <div>
                        <div>日均销售额<span class="value">￥12,423</span></div>
                    </div>
                </Card>
            </Col>
            <Col :xs="24" :sm="12" :md="6">
                <Card :dis-hover="true" class="mt20 mlr10">
                    <div>实时交易</div>
                    <div style="height:60px;"></div>
                    <div>
                        <div>日均销售额<span class="value">￥12,423</span></div>
                    </div>
                </Card>
            </Col>
            <Col :xs="24" :sm="12" :md="6">
                <Card :dis-hover="true" class="mt20 mlr10">
                    <div>实时交易</div>
                    <div style="height:60px;"></div>
                    <div class="flex-between">
                        <div>周同比<span class="value">12%</span></div>
                        <div>日环比<span class="value">11%</span></div>
                    </div>
                </Card>
            </Col>
        </Row>
        <Row class="plr10">
            <Col>
                <Card :bordered="false" :dis-hover="true" class="mt20 mlr10">
                    <div slot="title">实时交易</div>
                    <div style="position:relative">
                        <v-chart :force-fit="true" :height="350" :data="data1" :scale="scale" :padding="[50,40,50,40]">
                            <v-tooltip />
                            <v-axis />
                            <v-line :position="'year*value'" />
                            <v-point :position="'year*value'" />
                        </v-chart>
                    </div>
                </Card>
            </Col>
        </Row>
        <Row class="plr10">
            <Col :xs="24" :sm="12" :md="16">
                <Card :dis-hover="true" class="mt20 mlr10">
                    <div slot="title">实时交易</div>
                    <div>
                        <v-chart :force-fit="true" :height="280" :data="data3" :padding="[50,30,50,50]">
                            <v-tooltip />
                            <v-axis />
                            <v-legend />
                            <v-bar position="月份*月均降雨量" color="name" :adjust="adjust" />
                        </v-chart>
                    </div>
                </Card>
            </Col>
            <Col :xs="24" :sm="12" :md="8">
                <Card :dis-hover="true" class="mt20 mlr10">
                    <div slot="title">实时交易</div>
                    <div>
                        <v-chart :force-fit="true" :height="280" :data="data4" :scale="scale4" :padding="[0,0,0,0]">
                            <v-tooltip :show-title="false" :data-key="'item*percent'" />
                            <v-axis />
                            <v-legend :data-key="'item'" />
                            <v-pie :position="'percent'" :color="'item'" :v-style="pieStyle4" :label="labelConfig4"/>
                            <v-coord :type="'theta'" />
                        </v-chart>
                    </div>
                </Card>
            </Col>
        </Row>
        <Row class="plr10">
            <Col :xs="24" :sm="12" :md="12">
                <Card :dis-hover="true" class="mt20 mlr10">
                    <div slot="title">实时交易</div>
                    <div>
                        <v-chart :force-fit="true" height="400" :data="data2" :scale="scale2" :padding="[50,0,80,0]">
                            <v-tooltip :show-title="false" :data-key="'item*percent'"/>
                            <v-axis />
                            <v-legend :data-key="'item'"/>
                            <v-pie :position="'percent'" :color="'item'" :v-style="pieStyle" :label="labelConfig"/>
                            <v-coord :type="'theta'" />
                        </v-chart>
                    </div>
                </Card>
            </Col>
            <Col :xs="24" :sm="12" :md="12">
                <Card :dis-hover="true" class="mt20 mlr10">
                    <div slot="title">实时交易</div>
                    <div>
                        <v-chart force-fit="true" height="400" :data="data5" :scale="scale5" :padding="[10,0,10,0]">
                            <v-tooltip />
                            <v-axis :dataKey="axis1Opts5.dataKey" :line="axis1Opts5.line" :tickLine="axis1Opts5.tickLine" :grid="axis1Opts5.grid" />
                            <v-axis :dataKey="axis2Opts5.dataKey" :line="axis2Opts5.line" :tickLine="axis2Opts5.tickLine" :grid="axis2Opts5.grid" />
                            <v-legend data-key="user" marker="circle" offset="30" />
                            <v-coord type="polar" radius="0.8" />
                            <v-line position="item*score" color="user" size="2" />
                            <v-point position="item*score" color="user" size="4" />
                            <v-area position="item*score" color="user" />
                        </v-chart>
                    </div>
                </Card>
            </Col>
        </Row>
    </div>
</template>
<script>
    // import card from '../components/card'
    const data1 = [{ year: '1991', value: 3 }, { year: '1992', value: 4 }, { year: '1993', value: 3.5 }, { year: '1994', value: 5 }, { year: '1995', value: 4.9 }, { year: '1996', value: 6 }, { year: '1997', value: 7 }, { year: '1998', value: 9 }, { year: '1999', value: 13 }]

    const scale = [{
        dataKey: 'value',
        min: 0
    }, {
        dataKey: 'year',
        min: 0,
        max: 1
    }]
    const DataSet = require('@antv/data-set')

    const sourceData = [
        { item: '事例一', count: 40 },
        { item: '事例二', count: 21 },
        { item: '事例三', count: 17 },
        { item: '事例四', count: 13 },
        { item: '事例五', count: 9 }
    ]

    const scale2 = [{
        dataKey: 'percent',
        min: 0,
        formatter: '.0%'
    }]

    const dv = new DataSet.View().source(sourceData)
    dv.transform({
        type: 'percent',
        field: 'count',
        dimension: 'item',
        as: 'percent'
    })
    const data2 = dv.rows

    const sourceData3 = [
        { name: 'London', 'Jan.': 18.9, 'Feb.': 28.8, 'Mar.': 39.3, 'Apr.': 110, 'May': 47, 'Jun.': 20.3, 'Jul.': 24, 'Aug.': 35.6 }, { name: 'Berlin', 'Jan.': 12.4, 'Feb.': 23.2, 'Mar.': 34.5, 'Apr.': 99.7, 'May': 52.6, 'Jun.': 35.5, 'Jul.': 37.4, 'Aug.': 42.4 }
    ]
    const dv3 = new DataSet.View().source(sourceData3)
    dv3.transform({
        type: 'fold',
        fields: ['Jan.', 'Feb.', 'Mar.', 'Apr.', 'May', 'Jun.', 'Jul.', 'Aug.'],
        key: '月份',
        value: '月均降雨量'
    })
    const data3 = dv3.rows

    const sourceData4 = [
        { item: '事例一', count: 40 },
        { item: '事例二', count: 21 },
        { item: '事例三', count: 17 },
        { item: '事例四', count: 13 },
        { item: '事例五', count: 9 }
    ]

    const scale4 = [{
        dataKey: 'percent',
        min: 0,
        formatter: '.0%'
    }]

    const dv4 = new DataSet.View().source(sourceData4)
    dv4.transform({
        type: 'percent',
        field: 'count',
        dimension: 'item',
        as: 'percent'
    })
    const data4 = dv4.rows

    const sourceData5 = [
        { item: 'Design', a: 70, b: 30 },
        { item: 'Development', a: 60, b: 70 },
        { item: 'Marketing', a: 50, b: 60 },
        { item: 'Users', a: 40, b: 50 },
        { item: 'Test', a: 60, b: 70 },
        { item: 'Language', a: 70, b: 50 },
        { item: 'Technology', a: 50, b: 40 },
        { item: 'Support', a: 30, b: 40 },
        { item: 'Sales', a: 60, b: 40 },
        { item: 'UX', a: 50, b: 60 }
    ]

    const dv5 = new DataSet.View().source(sourceData5)
    dv5.transform({
        type: 'fold',
        fields: ['a', 'b'],
        key: 'user',
        value: 'score'
    })
    const data5 = dv5.rows

    const scale5 = [{
        dataKey: 'score',
        min: 0,
        max: 80
    }]

    const axis1Opts5 = {
        dataKey: 'item',
        line: null,
        tickLine: null,
        grid: {
            lineStyle: {
                lineDash: null
            },
            hideFirstLine: false
        }
    }
    const axis2Opts5 = {
        dataKey: 'score',
        line: null,
        tickLine: null,
        grid: {
            type: 'polygon',
            lineStyle: {
                lineDash: null
            }
        }
    }
    export default {
        components: {
            // card
        },
        data () {
            return {
                data1,
                scale,
                height: 400,
                data2,
                scale2,
                height2: 400,
                pieStyle: {
                    stroke: '#fff',
                    lineWidth: 1
                },
                labelConfig: ['percent', {
                    formatter: (val, item) => {
                        return item.point.item + ': ' + val
                    }
                }],
                data3,
                height3: 300,
                adjust: [{
                    type: 'dodge',
                    marginRatio: 1 / 32
                }],
                data4,
                scale4,
                height4: 300,
                pieStyle4: {
                    stroke: '#fff',
                    lineWidth: 1
                },
                labelConfig4: ['percent', {
                    offset: -40,
                    textStyle: {
                        rotate: 0,
                        textAlign: 'center',
                        shadowBlur: 2,
                        shadowColor: 'rgba(0, 0, 0, .45)'
                    }
                }],
                data5,
                scale5,
                padding5: [20, 20, 95, 20],
                axis1Opts5,
                axis2Opts5
            }
        }
    }
</script>
<style lang="less">
    
</style>


